.App {
  text-align: center;
  color: #073b4c;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #fde2e4;
  min-height: 100vh;
  justify-content: center;
}


.row{
  margin-bottom: 20px;
}

.label{
    display: inline-block;
    margin-right: 10px;
  }

.input{
    width: 40%;
    height: 25px;
    margin-right: 5px;
    margin-bottom: 5px;
    padding-left: 10px;
    outline: none;
  }

.button{
    padding: 10px 10px;
    width: 100px;
    border: none;
    background-color: #ef476f;
    color: #fefefe;
    font-weight: 400;
    cursor: pointer;
  }

  h3 {
    font-size: 2em;
    text-transform: uppercase;
    color: #118ab2;
  }

  hr {
    margin-top: 30px;
    text-align: center;
    width: 30%;
    box-shadow: 0px 6px 44px -9px rgba(0, 0, 0, 0.75);
  }